html {
  font-family: "Josefin Sans", sans-serif;
  font-size: 30px;
  height: 100%;
}

body {
  height: 100%;
  margin: 0;
}

#container {
  padding: 0;
  height: 100%;
  background: #fffef2;
}

#container .inner {
  width: 80%;
  margin: 0 auto;
  padding: 20px 0 0 0;
  max-width: 400px;
}

#icon {
  display: block;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  overflow: hidden;
  background-position: center center;
}
#icon img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

#title {
  text-align: center;
}

#caption {
  text-align: center;
  font-size: 10px;
}
